@import 'components/variables';

.section-team {
  padding-top: 100px;
  padding-bottom: 3rem;
  background: #efeff7;
  background-image: url(../images/team/bg-team.png);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: contain;
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .container {
    background: $white;
    border-radius: 25px;
    padding-bottom: 1.5rem;
    .bg-gray {
      background: $gray-light;
    }
    .bg-white {
      background: $white;
    }
    .py-item {
      padding-top: 3rem;
      padding-bottom: 3rem;
      @media (min-width: 992px) {
        padding-top: 6rem;
        padding-bottom: 6rem;
      }
    }
    .content-data {
      padding: 1rem;
      text-align: center;
      border-radius: 10px;
      @media (min-width: 992px) {
        padding: 1.5rem;
      }
      p {
        margin: 0;
      }
      .nombre {
        font-size: 1.2rem;
        font-weight: 600;
        color: $black;
        @media (min-width: 992px) {
          font-size: 1.5rem;
        }
      }
      .cargo {
        font-size: 1rem;
        font-weight: 400;
        @media (min-width: 992px) {
          font-size: 1.2rem;
        }
      }
      i {
        font-size: 2rem;
        transition: all 0.3s;
        &:hover {
          transform: scale(1.2);
        }
        &.icon-ln {
          color: #0e76a8;
        }
      }
    }
    .content-img {
      position: relative;
      img {
        height: calc(100% + 6rem);
        width: auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, calc(-50% - 1.5rem), 0);
        @media (min-width: 992px) {
          height: calc(100% + 12rem);
          transform: translate3d(-50%, calc(-50% - 3rem), 0);
        }
      }
      .img-2 {
        opacity: 0;
        transition: all 0.5s;
      }
      .img-1 {
        z-index: 2;
        opacity: 1;
        transition: all 0.5s;
        &:hover {
          opacity: 0;
        }
        &:hover ~ .img-2 {
          opacity: 1;
        }
      }
    }
  }
}